.modal {
  position: relative;
  max-width: 800px;
  box-shadow: 0px 3px 5px -1px rgba(0, 0, 0, 0.2),
    0px 5px 8px 0px rgba(0, 0, 0, 0.14), 0px 1px 14px 0px rgba(0, 0, 0, 0.12);
  background: #111;
  overflow: hidden;
  border-radius: 8px;
  transition: all 400ms ease-in-out 2s;
  animation: fadeIn 400ms;
  max-height: 90vh;
  overflow-y: auto;
}

.modal__poster-img {
  width: 100%;
  height: auto;
}

.modal__content {
  padding: 40px;
  color: white;
}

.modal__title {
  padding: 0;
  font-size: 40px;
  margin: 16px 0;
}

.modal__details {
  font-weight: 600;
  font-size: 18px;
}

.modal__overview {
  font-size: 20px;
  line-height: 1.5;
  margin-bottom: 20px;
}

.modal__user_perc {
  color: #46d369;
  font-size: 17px;
  font-weight: bold;
  margin-right: 10px;
}

/* .modal::-webkit-scrollbar {
  display: none;
  visibility: hidden;
} */

/* Hide scrollbar for IE, Edge and Firefox */
/* .modal { */
  /* -ms-overflow-style: none; */
  /* IE and Edge */
  /* scrollbar-width: none; */
  /* Firefox */
/* } */

.wrapper-modal {
  position: fixed;
  inset: 0px;
  background-color: rgb(0 0 0 / 71%);
  -webkit-tap-highlight-color: transparent;
  display: flex;
  justify-content: center;
  align-items: center;
}

.presentation {
  z-index: 1200;
  position: absolute;
}
.presentation .modal_details{
  font-size: 17px;
}
.modal-close {
  position: absolute;
  right: 20px;
  top: 5px;
  cursor: pointer;
  z-index: 1000;
  color: white;
  font-size: 30px;
}

@media (max-height: 768px) {
  .wrapper-modal {
    padding-top: 2rem;
  }

  .modal {
    overflow-y: scroll;
  }
}

@media (max-width: 768px) {
  .modal__overview {
    font-size: 16px;
  }

  .modal__details {
    font-size: 16px;
  }

  .wrapper-modal {
    padding: 0;
  }

  .modal {
    overflow-y: scroll !important;
  }
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: scale(0.5);
  }

  to {
    opacity: 1;
    transform: scale(1);
  }
}